<template>
  <div class="content">
    <h1>Articles and News</h1>
    <input placeholder="Search Articles..." class="search" :value="textFilter" v-on:input="updateTextFilterQuery">

    <div v-if="tagFilters.length > 0">
      <p class="filterText">Tag filters (click to remove):</p>
      <ul class="tagFilters">
        <li v-on:click="removeTagFilter(tag)" v-for="(tag, tagIndex) in tagFilters" :key="tagIndex">
          <tag-pill :text="tag" :cross="true"/>
        </li>
      </ul>
    </div>

    <article-list-item v-on:tag-click="addTagFilter" v-for="(article, articleIndex) in filteredArticles" :key="articleIndex" :article="article" :index="articleIndex" />
  </div>
</template>

<script>
import ArticleListItem from '@/components/ArticleListItem.vue'
import TagPill from '@/components/TagPill.vue'
import { articles } from '@/fake_articles.json'

export default {
  components: {
    ArticleListItem,
    TagPill
  },

  data () {
    return {
      articles
    }
  },

  methods: {
    pushNewTagsToQuery (newTags) {
      this.$router.push({
        query: {
          ...this.$route.query,
          tags: newTags && newTags.length > 0 ? newTags.join(',') : undefined
        }
      })
    },

    updateTextFilterQuery ({ target: { value } }) {
      this.$router.push({
        query: {
          ...this.$route.query,
          filter: value ? value : undefined
        }
      })
    },

    addTagFilter (tag) {
      if (!this.tagFilters.includes(tag)) {
        const newFilters = [...this.tagFilters, tag]
        this.pushNewTagsToQuery(newFilters)
      }
    },

    removeTagFilter (tag) {
      const newFilters = this.tagFilters.filter(el => el !== tag)
      this.pushNewTagsToQuery(newFilters)
    },

    getArticleURL (id) {
      return `/article/${id}`
    }
  },

  computed: {
    filteredArticles () {
      const { textFilter, tagFilters } = this
      const lowerCaseTagFilters = tagFilters.map(x => x.toLowerCase())

      function hasMatch (haystack, needle) {
        return haystack.toLowerCase().includes(needle.toLowerCase())
      }

      return this.articles
        .filter(article =>
          // Allow through if we have no tag filters, or if we do, allow through if the article has at least 1 tag match
          tagFilters.length === 0 || article.tags.some(tag => lowerCaseTagFilters.includes(tag.toLowerCase()))
        )
        .filter(article =>
          !textFilter || hasMatch(article.title, textFilter) || hasMatch(article.subtitle, textFilter) || article.tags.some(tag => hasMatch(tag, textFilter))
        )
    },

    textFilter () {
      return this.$route.query.filter || ''
    },

    tagFilters () {
      const { tags } = this.$route.query
      return tags ? tags.split(',') : []
    }
  }
}
</script>

<style lang="scss" scoped>

.filterText {
  font-family: 'Open Sans', sans-serif;
}

.search {
  border: none;
  transition: border 1s;
  border-bottom: 1.5px solid rgba(0,0,0,0.2);
  display: block;
  font-size: 1.5em;
  margin-bottom: 2vw;
}

textarea:focus, input:focus{
  outline: none;
  border-color: #2A9051;
  transition: border 1s;
}

.tagFilters {
  list-style: none;
  padding: 0;

  li {
    cursor: pointer;
    margin-right: 1vw;
    display: inline-block;
  }
}
</style>