<template>
  <div class="article-preview">
      <h3>
        <router-link :to="path">{{ article.title }}</router-link>
      </h3>
      <h4>
        {{ article.subtitle }}
      </h4>

      <p>{{ bodyPreview }}</p>
      <ul>
        <li v-on:click="onTagClick(tag)" v-for="(tag, tagIndex) in article.tags" :key="tagIndex">
          <tag-pill :text="tag" />
        </li>
      </ul>
  </div>
</template>

<script>
import removeMD from 'remove-markdown'
import TagPill from '@/components/TagPill.vue'

export default {
  props: ['article', 'index'],

  components: {
    TagPill
  },

  methods: {
    onTagClick (tag) {
      this.$emit('tag-click', tag)
    }
  },

  computed: {
    path () {
      if (this.article.slug) {
        return `/article/${this.article.slug}`
      }
      return `/article/${this.index}`
    },

    bodyPreview () {
      const { body } = this.article
      const noMarkdown = removeMD(body)
      const trimmed = noMarkdown.length > 100 ? noMarkdown.substr(0, 100) + '...' : noMarkdown

      return trimmed
    }
  }
}
</script>

<style lang="scss" scoped>
@media only screen
and (min-width : 680px) {

  ul {
    float: right;
    position: relative;
    top: -150px;
  }

}

.article-preview {
  padding: 1vw;
  padding-left: 0;
  text-align: left;
  // box-shadow: 0 0 10px 0 #888888;
  border-top: 1px solid rgba(0,0,0,0.2);
}

h3 {
  font-family: 'EB Garamond', serif;
  font-size: 1.7em;
  margin-top: 0;
}

h4 {
  color: rgba(0,0,0,0.5);
}

a {
  text-decoration: none;
  color: black;
}

ul {
  // float: right;
  list-style-type: none;
  // margin-top: 0.1vw;
  padding: 0;
}

li {
  &:last-of-type {
    margin-right: 0;
  }
  display: inline-block;
  margin-right: 15px;
  margin-bottom: 5px;
  cursor: pointer;
}
</style>
